.error {
	height: 100%;
	margin: 350px 0px 0px 0px;
	@media (max-width: 1100px) {
		margin: 550px 0px 0px 0px;
	}
	@media (max-width: 1020px) {
		margin: 350px 0px 0px 0px;
	}
	&__static {
		width: 100%;
		height: 100%;
		position: relative;
		margin: 0;
		padding: 0;
		top: -100px;
		opacity: 0.05;
		z-index: 230;
		user-select: none;
		user-drag: none;
	}
	&__title {
		text-align: center;
		font-family: serif;
		font-size: 95px;
		font-style: italic;
		text-align: center;
		width: 100px;
		height: 60px;
		line-height: 60px;
		margin: auto;
		position: absolute;
		top: 0;
		bottom: 0;
		left: -160px;
		right: 0;
		animation: noise 2s linear infinite;
		overflow: default;
		@media (max-width: 1030px) {
			left: -50px;
		}
		&:after {
			content: "404";
			font-family: serif;
			font-size: 100px;
			font-style: italic;
			text-align: center;
			width: 150px;
			height: 60px;
			line-height: 60px;
			margin: auto;
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			opacity: 0;
			color: blue;
			animation: noise-1 0.2s linear infinite;
		}
	}
	&__info {
		text-align: center;
		font-family: serif;
		font-size: 45px;
		font-style: italic;
		text-align: center;
		width: 280px;
		height: 60px;
		line-height: 60px;
		margin: auto;
		position: absolute;
		top: 160px;
		bottom: 0;
		left: -130px;
		right: 0;
		animation: noise-3 1s linear infinite;
		@media (max-width: 1030px) {
			left: 0px;
		}
		&:before {
			content: "404";
			font-family: serif;
			font-size: 100px;
			font-style: italic;
			text-align: center;
			width: 100px;
			height: 60px;
			line-height: 60px;
			margin: auto;
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			opacity: 0;
			color: red;
			animation: noise-2 0.2s linear infinite;
		}
	}
}

@keyframes noise-1 {
	0%,
	20%,
	40%,
	60%,
	70%,
	90% {
		opacity: 0;
	}
	10% {
		opacity: 0.1;
	}
	50% {
		opacity: 0.5;
		left: -6px;
	}
	80% {
		opacity: 0.3;
	}
	100% {
		opacity: 0.6;
		left: 2px;
	}
}

@keyframes noise-2 {
	0%,
	20%,
	40%,
	60%,
	70%,
	90% {
		opacity: 0;
	}
	10% {
		opacity: 0.1;
	}
	50% {
		opacity: 0.5;
		left: 6px;
	}
	80% {
		opacity: 0.3;
	}
	100% {
		opacity: 0.6;
		left: -2px;
	}
}

@keyframes noise {
	0%,
	3%,
	5%,
	42%,
	44%,
	100% {
		opacity: 1;
		transform: scaleY(1);
	}
	4.3% {
		opacity: 1;
		transform: scaleY(1.7);
	}
	43% {
		opacity: 1;
		transform: scaleX(1.5);
	}
}

@keyframes noise-3 {
	0%,
	3%,
	5%,
	42%,
	44%,
	100% {
		opacity: 1;
		transform: scaleY(1);
	}
	4.3% {
		opacity: 1;
		transform: scaleY(4);
	}
	43% {
		opacity: 1;
		transform: scaleX(10) rotate(60deg);
	}
}
